<script>
  import { games, genres } from "../../stores";

  let name;
  let systemId;
  let releaseDate;
  let genreIds = [];

  const systems = [
    { name: "Playstation 4", id: 1 },
    { name: "PC", id: 2 },
    { name: "Playstation 3", id: 3 },
    { name: "Xbox 360", id: 4 },
    { name: "Nintendo DS", id: 5 },
    { name: "Android", id: 6 },
    { name: "Gamecube", id: 7 },
    { name: "Wii", id: 8 },
    { name: "Super Nintendo", id: 9 },
    { name: "Genesis", id: 10 },
    { name: "Playstation", id: 11 }
  ];

  async function submit() {
    await games.create({ name, systemId, releaseDate, genreIds });
    name = "";
    genreIds = [];
    systemId = 1;
  }
</script>

<style>
  .select {
    height: 100%;
  }
  .input {
    max-width: 350px;
  }
</style>

<div class="content">
  <h2 class="title">Add Game</h2>

  <form on:submit|preventDefault={submit}>
    <div class="field">
      <label class="label">Game name</label>
      <div class="control">
        <input class="input" type="text" bind:value={name} />
      </div>
    </div>

    <div class="field">
      <label class="label">Console</label>
      <div class="control">
        <div class="select">
          <select bind:value={systemId}>
            {#each systems as system}
              <option value={system.id}>{system.name}</option>
            {/each}
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <label class="label">Release date</label>
      <div class="control">
        <input
          type="text"
          class="input"
          bind:value={releaseDate}
          placeholder="MM/DD/YYYY" />
      </div>
    </div>

    <div class="field">
      <label class="label">Genres</label>
      <div class="control">
        <div class="select is-multiple">
          <select bind:value={genreIds} multiple size="8">
            {#each $genres.items as genre}
              <option value={genre.id}>{genre.name}</option>
            {/each}
          </select>
        </div>
      </div>
    </div>

    <div class="field">
      <div class="control">
        <button class="button is-link" type="submit" disabled={!name}>
          Create
        </button>
      </div>
    </div>

  </form>
</div>
