<script>
  import Spinner from "../layout/Spinner.svelte";
  import { games, thoughts } from "../../stores";

  let rating = 7;
  let text;
  let isReplayable = false;
  let hoursPlayed;
  let gameId;

  const ratings = [
    { value: 0, text: "Hated it" },
    { value: 5, text: "Felt indifferent" },
    { value: 7, text: "Liked it" },
    { value: 10, text: "Loved it" }
  ];

  async function submit() {
    await thoughts.create({
      rating,
      text,
      isReplayable,
      hoursPlayed: parseInt(hoursPlayed),
      gameId
    });
    rating = 7;
    text = "";
    isReplayable = false;
  }
</script>

<style>
  .input {
    max-width: 350px;
  }
</style>

{#if $games.loading}
  <Spinner />
{:else}
  <div class="content">
    <h1 class="title">Add Thoughts</h1>

    <form on:submit|preventDefault={submit}>

      <div class="field">
        <label class="label">What game?</label>
        <div class="control">
          <div class="select">
            <select bind:value={gameId}>
              {#each $games.items as game}
                <option value={game.id}>{game.name}</option>
              {/each}
            </select>
          </div>
        </div>
      </div>

      <div class="field">
        <div class="control">
          <label class="checkbox">
            <input type="checkbox" bind:checked={isReplayable} />
            Is it replayable?
          </label>
        </div>
      </div>

      <div class="field">
        <label class="label">I...</label>
        <div class="control">
          <div class="select">
            <select bind:value={rating}>
              {#each ratings as r}
                <option value={r.value}>{r.text}</option>
              {/each}
            </select>
          </div>
        </div>
      </div>

      <div class="field">
        <label class="label">How many hours did you play?</label>
        <div class="control">
          <input type="text" class="input" bind:value={hoursPlayed} />
        </div>
      </div>

      <div class="field">
        <label class="label">What are your thoughts?</label>
        <div class="control">
          <textarea class="textarea" type="text" bind:value={text} />
        </div>
      </div>

      <div class="field">
        <div class="control">
          <button class="button is-link" type="submit" disabled={!text}>
            Create
          </button>
        </div>
      </div>

    </form>
  </div>
{/if}
