<script>
  import { scale } from "svelte/transition";
  import { navigate } from "svelte-routing";
  import md5 from "blueimp-md5";
  import { auth } from "../stores";
  import { post, get } from "../utils";

  let email = "";
  let password = "";
  let error = false;
  let isLoading = false;
  let src = "";

  function getGravatar() {
    email;
    if (!email.match(/[^@]+@[^\.]+\..+/)) {
      src = "";
      return;
    }
    src = `https://www.gravatar.com/avatar/${md5(email)}?s=128&d=identicon`;
  }

  async function submit() {
    error = false;
    isLoading = true;
    try {
      const result = await post("/auth/login", {
        email,
        password
      });

      const profile = await get("/profile", result.access_token);
      console.log(profile);
      auth.login(email, result);
      navigate("/activities", { replace: true });
    } catch (err) {
      console.error(err);
      error = true;
      isLoading = false;
    }
  }
</script>

<style>
  .box {
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transition: height 500ms;
    height: 234px;
  }

  .box.with-avatar {
    height: 328px;
  }

  .login-hr {
    border-bottom: 1px solid black;
  }

  .avatar {
    margin-top: -70px;
    padding-bottom: 20px;
  }

  .avatar img {
    padding: 5px;
    background: #fff;
    border-radius: 50%;
    box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  }

  .field {
    padding-bottom: 10px;
  }
</style>

<section class="hero is-light is-fullheight-with-navbar">
  <div class="hero-body">
    <div class="container has-text-centered">
      <div class="column is-4 is-offset-4">
        <h3 class="title has-text-black">Login</h3>
        <hr class="login-hr" />
        <p class="subtitle has-text-black">Please login to proceed.</p>
        <div class="box {src ? 'with-avatar' : ''}">
          {#if src}
            <figure class="avatar" transition:scale={{ duration: 500 }}>
              <img {src} />
            </figure>
          {/if}
          <form on:submit|preventDefault={submit}>
            <div class="field">
              <div class="control">
                <input
                  class="input is-medium"
                  type="email"
                  placeholder="Email"
                  bind:value={email}
                  on:blur={getGravatar}
                  autofocus="" />
              </div>
            </div>

            <div class="field">
              <div class="control">
                <input
                  class="input is-medium {error ? 'is-danger' : ''}"
                  type="password"
                  placeholder="Password"
                  bind:value={password} />
              </div>
              {#if error}
                <p class="help is-danger">
                  Please enter a valid username or password
                </p>
              {/if}
            </div>
            <button
              class="button is-block is-info is-medium is-fullwidth {isLoading ? 'is-loading' : ''}"
              disabled={!email || !password || isLoading}>
              Login
              <i class="fa fa-sign-in" aria-hidden="true" />
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</section>
